  .navbar .navbar-toggler {
    height: 16px;
    width: 23px;
    cursor: pointer;
    padding: 0;
    border: 0;
    background: transparent;
    outline: 0;
    box-shadow: none;
    z-index: 9;
    position: relative; }
    .navbar .navbar-toggler > span {
      display: block;
      position: absolute;
      height: 2px;
      width: 100%;
      background: #fff none repeat scroll 0% 0%;
      opacity: 1;
      top: 0;
      left: 0px;
      -webkit-transition: all 600ms cubic-bezier(0.62, 0.04, 0.3, 1.56);
      transition: all 600ms cubic-bezier(0.62, 0.04, 0.3, 1.56);
      -webkit-transition-delay: 100ms;
              transition-delay: 100ms;
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg); }
      .navbar .navbar-toggler > span:nth-child(1) {
        margin-top: 0px; }
      .navbar .navbar-toggler > span:nth-child(2) {
        margin-top: 7px;
        margin-right: 0px; }
      .navbar .navbar-toggler > span:nth-child(3) {
        margin-top: 14px; }
    .navbar .navbar-toggler.collapsed span {
      background-color: #fff !important; }
      .navbar .navbar-toggler.collapsed span:nth-child(1) {
        -webkit-transform: rotate(135deg);
                transform: rotate(135deg);
        margin-top: 7px; }
      .navbar .navbar-toggler.collapsed span:nth-child(2) {
        opacity: 0;
        left: -69px; }
      .navbar .navbar-toggler.collapsed span:nth-child(3) {
        -webkit-transform: rotate(-135deg);
                transform: rotate(-135deg);
        margin-top: 7px; }
    @media (min-width: 992px) {
      .navbar .navbar-toggler {
        display: none; } }
  .navbar .nav-link {
    font-size: 14px;
    font-size: 1rem;
    margin: 0;
    font-weight: bold;
    line-height: normal;
    color: #fff;
    position: relative; }
    .navbar .nav-link:after {
      content: "";
      position: absolute;
      width: 100%;
      height: 1px;
      bottom: -1px;
      left: 0;
      background-color: #fa3840;
      visibility: hidden;
      -webkit-transform: scaleX(0);
              transform: scaleX(0);
      -webkit-transition: all 0.3s ease-in-out 0s;
      transition: all 0.3s ease-in-out 0s; }
    .navbar .nav-link:hover:after, .navbar .nav-link.active:after {
      visibility: visible;
      -webkit-transform: scaleX(1);
              transform: scaleX(1); }
    @media (max-width: 991px) {
      .navbar .nav-link {
        padding: 15px 0;
