    @media (min-width: 992px) {
      .navbar .nav-link {
        margin: 0 25px; }
        .navbar .nav-link:after {
          height: 3px;
          bottom: -3px; } }
  .navbar.sticky {
    top: 0;
    background-color: #fff;
    box-shadow: 0 2px 16px 0 rgba(0, 0, 0, 0.14);
    padding: 15px 0; }
    @media (max-width: 991px) {
