@import "abstracts/mixins";
@import "base/typography";
@import "pages/header";

section {
  +section {
    +section {
      background-color: #fff;
      height: 155vh;
      color: $color-black;
      background-color: $color-black;
      color: #fff;
      text-align: center;
      color: #ffffff;
      display: flex;
      align-items: center;
      justify-content: center;
      font-family: Open Sas;
      +section {
        background-color: #fff;
        height: 30vh;
        color: $color-black;
        background-color: $color-black;
        color: #fff;
        text-align: center;
        color: #ffffff;
        display: flex;
        align-items: center;
        justify-content: center;
      }
    }
  }
}